import { FreeMode } from "swiper/modules";
import { Swiper, SwiperSlide } from "swiper/react";
import TeletypeCarousel from "./teletypeCarousel";
import profile_test from "./../images/profile_test.jpg";
import useWindowDimensions from "./GetDimensions";
import "swiper/css";
import "swiper/css/free-mode";

function ProductCarousel() {
  return (
    <div>
      <div>
        <Swiper slidesPerView={1} modules={[FreeMode]}>
          <SwiperSlide>
            <img src={profile_test}></img>
          </SwiperSlide>
          <SwiperSlide>
            <img src={profile_test}></img>
          </SwiperSlide>
          <SwiperSlide>
            <img src={profile_test}></img>
          </SwiperSlide>
        </Swiper>
      </div>
      <div id="main_product_info">
        <p id="main_info_product_name">Apple AirPods Max Silver</p>
        <div className="select_currency">
          <p>Выберите валюту</p>
          <select id="currency_choose" onchange="changeCurrency(event)">
            <option value="rub">RUB</option>
            <option value="eur">EUR</option>
            <option value="byn">BYN</option>
          </select>
        </div>
        <div id="main_info_product_price">
          <p id="gold_price">4 687 ₽</p>
          <div id="main_info_currencylogo">₽ RUB</div>
        </div>
        <div id="choose_color">
          <p>Цвет корпуса</p>
          <div className="color_variants">
            <img className="variant" src={profile_test}></img>
            <img className="variant" src={profile_test}></img>
            <img className="variant" src={profile_test}></img>
          </div>
        </div>
        <div id="stories">
          <p>Полезная информация</p>
          <Swiper slidesPerView={3}>
            <SwiperSlide>
              <div className="story_block">
                <div>
                  <p>Заголовок статьи в три строки</p>
                </div>
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div className="story_block">
                <div>
                  <p>Заголовок статьи в три строки</p>
                </div>
              </div>
            </SwiperSlide>
            <SwiperSlide>
              <div className="story_block">
                <div>
                  <p>Заголовок статьи в три строки</p>
                </div>
              </div>
            </SwiperSlide>
          </Swiper>
        </div>
      </div>
    </div>
  );
}
export default ProductCarousel;
